import CoinPairInfo from '@/components/common/CoinPairInfo'
import usePositionListAction from '@/hooks/position/usePositionListAction'
import useGlobalStore from '@/store/common/global'
import useDlmmPositionStore from '@/store/dlmm-position'
import usePoolsStore from '@/store/pool'
import { PosBaseInfo } from '@/types'
import { Block } from '@cetus/design'
import useWindowWidth from '@cetus/hooks/src/useWindowWidth'
import { Icon } from '@cetus/ui-kit'
import { cancelBubble } from '@cetus/utils'
import { Button, Collapse, HStack, Stack, Text, VStack } from '@chakra-ui/react'
import { useMemo, useState } from 'react'
import { useNavigate } from 'react-router-dom'
import PositionCurrentPrice from '../../common/PositionCurrentPrice'
import DLMMPositionItem from './DLMMPositionItem'

function DLMMPoolItem({ poolInfo, createAble = true }: { poolInfo: any; createAble?: boolean }) {
  const navigate = useNavigate()
  const { dlmmPosPoolsRelatedData, dlmmPosLiquidityData } = useDlmmPositionStore()
  const { toCloseAllAction, isRemoveLoading } = usePositionListAction()
  const { isExpendPositionMap, setIsExpendPosition } = usePoolsStore()

  const closeAllPositionList = useMemo(() => {
    return poolInfo?.list?.filter((item: PosBaseInfo) => item.posType !== 'burn') || []
  }, [poolInfo?.list])

  const haveFarming = dlmmPosLiquidityData[poolInfo?.dlmmPoolAddress]?.haveFarming
  const feeDisplay = (dlmmPosPoolsRelatedData[poolInfo?.list?.[0]?.id]?.displayFee || '--') + '%'
  const binStep = dlmmPosPoolsRelatedData[poolInfo?.list?.[0]?.id]?.binStep || '--'
  const toCloseAll = async () => {
    if (closeAllPositionList.length) toCloseAllAction(closeAllPositionList)
  }
  const { setBackUrl } = useGlobalStore()

  const [priceDirect, setPriceDirect] = useState(true)
  const { isApp } = useWindowWidth()

  const isExpand = useMemo(() => {
    return isExpendPositionMap[poolInfo?.dlmmPoolAddress] ?? false
  }, [isExpendPositionMap])

  const onExpand = () => {
    setIsExpendPosition([poolInfo?.dlmmPoolAddress], !!!isExpand)
  }

  return (
    <Block w="100%" p={isApp ? '4px 12px 16px' : isExpand ? '12px 16px 16px' : '12px 16px 8px'} borderRadius="16px">
      <VStack w="100%" gap={{ base: '12px', lg: '12px' }}>
        <Stack
          direction={{ base: 'column', lg: 'row' }}
          justify="space-between"
          w="100%"
          gap={{ base: '0px', lg: '8px' }}
          cursor="pointer"
          onClick={onExpand}
        >
          {/* <PoolShowInfo
            symbolFontWeight="500"
            symbolFontSize="16px"
            symbolEllipsesDecimals={10}
            nameEllipsesDecimals={20}
            poolInfo={{ feeDisplay, ...poolInfo }}
            haveName
            haveFarming={haveFarming}
          /> */}
          <CoinPairInfo
            type="column"
            poolType="dlmm"
            symbolFontWeight="500"
            symbolFontSize="16px"
            symbolEllipsesDecimals={10}
            nameEllipsesDecimals={20}
            poolInfo={{ feeDisplay, binStep, ...poolInfo, poolAddress: poolInfo?.dlmmPoolAddress }}
            haveFarming={haveFarming}
            versionBlockPosition="right"
            showPoolTypeTag
            moreDetails
            clickFun={() => navigate(`/dlmm?poolId=${poolInfo?.dlmmPoolAddress}`)}
          />
          <HStack flexDirection={{ base: 'column', lg: 'row' }} gap={{ base: '12px', lg: '8px' }}>
            <HStack
              mr="0px"
              w={{ base: '100%', lg: 'unset' }}
              justify={{ base: 'space-between', lg: 'flex-start' }}
              flexWrap={{ base: 'wrap', lg: 'nowrap' }}
            >
              <Text color="primary_gray">Current Price</Text>
              <PositionCurrentPrice
                symbolEllipsesDecimals={10}
                posId={poolInfo?.list?.[0]?.id}
                displayTokenA={poolInfo?.displayTokenA}
                displayTokenB={poolInfo?.displayTokenB}
                handleDirect={() => setPriceDirect(!priceDirect)}
              />
            </HStack>
            <HStack w={{ base: '100%', lg: 'unset' }} flexDirection={{ base: 'column', lg: 'row' }} justify="space-between">
              <Button
                w={{ base: '100%', lg: 'unset' }}
                onClick={e => {
                  cancelBubble(e)
                  setBackUrl('/pools?tab=positions')
                  navigate(`/dlmm?poolId=${poolInfo?.dlmmPoolAddress}`)
                }}
                borderRadius="8px"
                p={{ base: '0 8px', lg: '0 12px' }}
                h="32px"
                fontWeight="500"
                variant="outline"
                bg="primary_opacity.10"
                fontSize="12px"
              >
                Create Position
              </Button>
              <Block
                cursor="pointer"
                w={{ base: '100%', lg: 'unset' }}
                p="4px 0 4px 8px"
                h="32px"
                borderRadius="8px"
                bg="none"
                border={{ base: '1px solid', lg: 'none' }}
                borderColor="border"
                _hover={{
                  svg: { fill: 'text_caption' },
                  p: { color: 'text_caption' }
                }}
              >
                <HStack justify="center" w="100%" h="100%" gap="4px">
                  <Text fontSize="12px">
                    {poolInfo?.list?.length} {poolInfo?.list?.length > 1 ? 'Positions' : 'Position'}
                  </Text>
                  <Icon
                    xlinkHref="#icon-icon_descending_nor"
                    fontSize="20px"
                    transition="transform 0.5s"
                    transform={isExpand ? 'rotate(-180deg)' : 'rotate(0deg)'}
                  />
                </HStack>
              </Block>
            </HStack>
          </HStack>
        </Stack>
        <Collapse
          unmountOnExit
          animateOpacity
          in={isExpand}
          style={{ width: '100%' }}
          transition={{
            enter: { duration: 0.2, ease: 'easeOut' },
            exit: { duration: 0.2, ease: 'easeIn' }
          }}
        >
          <DLMMPositionItem poolInfo={poolInfo} priceDirect={priceDirect} />
        </Collapse>
      </VStack>
    </Block>
  )
}

export default DLMMPoolItem
